<script>

import api from "../utils/api";

export default {
  name: "Home",
  created() {
    api.get("/menu?type=carousel").then(
      data => {
        this.menuCarousel = data;
      },
      err => {}
    );

    api.get("/menu?type=index").then(
      data => {
        this.menuIndex = data;
        // console.log(data);
      },
      err => {}
    );
    document.title = "小米商城";
    // 自动轮播
    // setInterval(this.carousel, 3000);
  },

  data() {
    return {
      menuCarousel: [],
      menuIndex: [],
      showIndex: 0
    };
  },

  methods: {
    carousel() {
      this.showIndex == this.menuCarousel.length - 1
        ? (this.showIndex = 0)
        : this.showIndex++;
    }
  },

  components: {
    // "app-header": header
  }
};
</script>

