<template>
  <div>
    <div class="topbar">
      <div class="container">
        <div class="topbar-nav">
          <a href>小米商城</a>
          <span>|</span>
          <a href>MIUI</a>
          <span>|</span>
          <a href>IoT</a>
          <span>|</span>
          <a href>云服务</a>
          <span>|</span>
          <a href>金融</a>
          <span>|</span>
          <a href>有品</a>
          <span>|</span>
          <a href>小爱开放平台</a>
          <span>|</span>
          <a href>政企服务</a>
          <span>|</span>
          <a href>Select Region</a>
        </div>
        <div class="topbar-cart logined" v-if="isLoggedIn">
          <!-- 点击购物车跳转到购物车页面 -->
          <router-link :to="{name:'cart'}">
            <i class="iconfont">&#xe60c;</i>购物车
            <span>
              （
              <label class="count">{{totalCartGoods}}</label>）
            </span>
          </router-link>
        </div>
        <div class="topbar-info clearfix">
          <!-- 点击登录按钮，跳转到登录页面，切换路由，所以使用router-link -->
          <!-- to属性，要跳转的路由 -->
          <template v-if="!isLoggedIn">
            <router-link to="/login" class="not-logined" @click.native="changeIsLogin(true)">登录</router-link>
            <span class="not-logined">|</span>
            <router-link to="/login" class="not-logined" @click.native="changeIsLogin(false)">注册</router-link>
            <span class="not-logined">|</span>
          </template>

          <template v-else>
            <a class="logined username">{{user.name}}</a>
            <span class="logined">|</span>
            <a class="sep">消息通知</a>
            <a href="#" class="sep logined logout" v-if="isLoggedIn" @click="logout">退出</a>
          </template>
        </div>
      </div>
    </div>

    <div class="header" v-show="routeName!='cart'">
      <div class="container">
        <div class="header-logo">
          <router-link to="/" class="lr">小米官网</router-link>
        </div>
        <div class="header-nav">
          <ul class="nav-list clearfix">
            <li class="nav-category" :class="{otherPage:routeName!='Home'}">
              <a href="javascript:;" :class="{hidden:routeName=='Home'}">全部商品分类</a>
              <div class="category-list">
                <ul>
                  <li class="tpl-cat" v-for="(left,index) in menuLeft" :key="`left_${index}`">
                    <a class="title">
                      <span>{{left.name}}</span>
                      <i class="iconfont"></i>
                    </a>
                    <!-- v-if="left.list",left中有list就显示这个 -->
                    <div class="children clearfix" v-if="left.list">
                      <ul class="children-list">
                        <li
                          class="tpl-children"
                          v-for="(list,index) in left.list"
                          :key="`list_${index}`"
                        >
                          <a href="#">
                            <img :src="list.img" alt />
                            <span class="text">{{list.name}}</span>
                          </a>
                        </li>
                      </ul>
                    </div>
                  </li>
                </ul>
              </div>
            </li>
            <li class="nav-item tpl-nav" v-for="(top,index) in menuTop" :key="`top_${index}`">
              <a>{{top.name}}</a>
            </li>
          </ul>
        </div>
        <div class="header-search">
          <form action class="search-form">
            <input type="search" name="keyword" class="search-text" />
            <button class="search-btn iconfont">&#xe616;</button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "../utils/api";
import { mapState, mapGetters } from "vuex";

export default {
  created() {
    // 在打开页面，或刷新页面的时候，会触发钩子函数,这时候发送请求，通过?发送请求参数,传top请求头部数据
    api.get("/menu?type=top").then(
      response => {
        // 使用箭头函数，因为不使用箭头函数的话，this指向是不对的(这种情况可以在函数外面_this=this来保存this)
        this.menuTop = response;
      },
      error => {}
    );
    api.get("/menu?type=left").then(
      response => {
        this.menuLeft = response;
      },
      error => {}
    );
    // 获取商品总数量
    // this.$store.dispatch("cart/CHANGEcartGoodsList");
    // console.log(this.totalCartGoods)
  },

  async mounted() {
    // 如果登录就获取，如果没有登录就不获取
    if (this.isLoggedIn) {
      // await等这个请求执行完
      await this.$store.dispatch("cart/CHANGEcartGoodsList");
      this.totalCartGoods = this.cartGoodsList.reduce((sum, item) => {
        return (sum += Number(item.quantity));
      }, 0);
    }
  },

  data() {
    return {
      menuTop: [],
      menuLeft: [],
      // routeName当前所在页面的路由
      routeName: this.$route.name,
      totalCartGoods: 0
    };
  },

  computed: {
    ...mapState("user", ["isLoggedIn", "user", "isLogin"]),
    ...mapState("cart", ["cartGoodsList"])
    // ...mapGetters("cart", ["totalCartGoods"])
  },

  watch: {
    cartGoodsList() {
      this.totalCartGoods = this.cartGoodsList.reduce((sum, item) => {
        return (sum += Number(item.quantity));
      }, 0);
    }
  },

  methods: {
    logout() {
      this.$store.commit("user/logout");
    },
    changeIsLogin(val) {
      this.$store.commit("user/changeIsLogin", val);
    }
  }
};
</script>

<style lang="scss" scoped>
// sass的语法，引入其它的样式文件
// 引入的文件名需要带后缀，如.css
@import url(../css/iconfont.css);
@import url(../css/style.css);

// 在不是home页面的时候，就获得otherPage类
.otherPage .category-list > ul{
    background: #fff;
    border: 1px solid #ff6700;
}
.otherPage .category-list .title{
    color: #424242;
}
.otherPage .category-list .title i{
    color: #e0e0e0;
    position: absolute;
    top: 12px;
    right: 20px;
    font-size: 16px;
    line-height: 16px;
}
.otherPage .category-list {
    display: none;
}
.otherPage:hover .category-list{
    display: block;
}

</style>